<template>
	<view class="flex flex-direction bg-white">
		<!-- 判断是否登录 -->
		<is-login currentPage="index" ></is-login>
		<!-- 导航栏 -->
		<cu-custom bgColor="bg-cyan" :isBack="preview">
			<block slot="content">员工管理</block>
		</cu-custom>
		<!-- 搜索员工信息 -->
		<luyj-search-bar placeholder="请输入员工名称或编码" @confirm="search" @cancel="cancel" ></luyj-search-bar>
		<!-- 员工列表 -->
		<uni-list>
			<!-- 员工列表 -->
			<uni-list-item showArrow v-for="(item , index) in staff_list" :key="index" :title="item.Name + '(' + item.StaffNo + ')'"
			 :note="item.Mobile"
			 :thumb="item.Avatar" thumbSize="lg"
			 :to="'/pages/basic/staff/staffdetail?preview=true&StaffId=' + item.StaffId"></uni-list-item>
			 <!-- 加载更多 -->
			 <uni-load-more status="noMore"></uni-load-more>
		</uni-list>
	</view>
</template>

<script>
	/**
	 * 员工信息列表
	 * @author 陆彦捷
	 * @date 2021.06.29 uni-list 形式列出当前组织的所有信息
	 * @date 2021.07.01 添加搜索栏
	 * @date 2021.07.23 重新引用login
	 */
	import organ from '@/common/storage/organstorage.js';		// 组织
	var _self;
	export default {
		data() {
			return {
				// 与样式有关参数
				preview : false,	// 是否返回上一页
				// 与系统有关参数
				MchId : 0 ,			// 商户id(暂时不做列表和进一步搜索)（认为是当前组织Id）
				// OrgId : 0 ,			// 组织id（暂时不做列表和进一步搜索）
				Token : '',				// 当前的Token值
				staff_list :[],			// 员工信息
			}
		},
		onLoad:function(options){
			_self = this;
			this.getParams(options);		// 初始化信息
			this.stafflist_request();		// 获取员工信息
		},
		methods: {
			// ====================================== 初始化信息 ============================================================================
			/** 初始化数据
			 * @param {Object} options
			 */
			getParams : function(options){
				this.preview = options.preview != 'false' ? Boolean(options.preview): false;
				this.MchId = organ.getIndustryId();
				this.Token = this.global.login.getToken();
			},
			// ====================================== 监听事件 ============================================================================
			/** 搜索当前员工信息
			 * @param {Object} e
			 */
			search : function(e){
				console.log("搜索当前员工信息");
			},
			/** 取消搜索员工信息
			 * @param {Object} e
			 */
			cancel : function(e){
				console.log("取消搜索当前员工信息");
			},
			// ====================================== 请求事件 ============================================================================
			/**
			 * 获取员工信息
			 */
			stafflist_request : function(){
				uni.showLoading();
				uni.request({
					url : _self.website + '/api/SvcMchStaff/Get?mchId=' + _self.MchId,
					header: {
						Authorization: 'Bearer ' + _self.Token
					},
					method: 'GET',
					success: function(result) {
						uni.hideLoading();
						console.log("success=>" , result);
						if (result.statusCode == 200) {
							if (result.data.ResultCode == "0") {
								_self.staff_list = result.data.Data;
								return;
							}
						}
					},
					fail: function(erros) {
						uni.hideLoading();
						console.log("fail =>" , erros);
						_self.staff_list = [];
						uni.showToast({
							title:'加载数据失败！',
							icon:'none'
						});
					}
				})
			}
			// ===============================================================================================================================
		}
	}
</script>

